<template>
    <div>
        <div v-show="selectmodule" class="localgloblgl  localgloblgl--custom">
            <div class="btns">
                <div class="search float-r">
                <el-input class="search-input" placeholder="租户名/租户号" icon="search" v-model="searchInput" :on-icon-click="handleCompanyInfoSearch">
                </el-input>
                </div>
            </div>
            <div class="tables">
                <el-table class="table" ref="multipleTable" :data="companyInfoData" border tooltip-effect="dark" @selection-change="handleSelectionChange">
                    <el-table-column prop="companyid" label="企业账号" width="150"></el-table-column>
                    <el-table-column prop="companyname" label="企业名称" width="150"></el-table-column>
                    <el-table-column label="操作">
                        <template slot-scope="scope">
                            <el-button type="text" size="large" style="margin-right:20px;" @click="handlejump(scope.$index, scope.row, 'ext')">分机管理</el-button>
                            <el-button type="text" size="large" style="margin-right:20px;" @click="handlejump(scope.$index, scope.row, 'extg')">分机组管理</el-button>
                            <el-button type="text" size="large" style="margin-right:20px;" @click="handlejump(scope.$index, scope.row, 'schedule')">工作时间</el-button>
                            <el-button type="text" size="large" style="margin-right:20px;" @click="handlejump(scope.$index, scope.row, 'callerBlackLists')">主叫黑名单</el-button>
                            <el-button type="text" size="large" style="margin-right:20px;" @click="handlejump(scope.$index, scope.row, 'calleeBlackLists')">被叫黑名单</el-button>
                            <el-button type="text" size="large" style="margin-right:20px;" @click="handlejump(scope.$index, scope.row, 'ivrFlowMenu')">IVR主菜单</el-button>
                            <el-button type="text" size="large" style="margin-right:20px;" @click="handlejump(scope.$index, scope.row, 'ivrEntry')">IVR子菜单</el-button>
                            
                        </template>
                    </el-table-column>
                </el-table>
            </div>
            <div class="float-r Pagination" style="text-align: left;margin-top: 10px;">
                <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage" :page-size="10" layout="total, prev, pager, next" :total="count">
                </el-pagination>
            </div>
        </div>
        <div v-show="!selectmodule">
            <ext v-if="selectMenuName=='ext'" @rebackEvent="rebackInvoke" :rebackButton="showRebackButton" :companyId="companyId"></ext>
            <extg  v-else-if="selectMenuName=='extg'" @rebackEvent="rebackInvoke" :rebackButton="showRebackButton" :companyId="companyId"></extg>
            <schedule  v-else-if="selectMenuName=='schedule'" @rebackEvent="rebackInvoke" :rebackButton="showRebackButton" :companyId="companyId"></schedule>

            <ivrEntry  v-else-if="selectMenuName=='ivrEntry'" @rebackEvent="rebackInvoke" :rebackButton="showRebackButton" :companyId="companyId"></ivrEntry>
            <ivrFlowMenu  v-else-if="selectMenuName=='ivrFlowMenu'" @rebackEvent="rebackInvoke" :rebackButton="showRebackButton" :companyId="companyId"></ivrFlowMenu>
            <callerBlackLists  v-else-if="selectMenuName=='callerBlackLists'" @rebackEvent="rebackInvoke" :rebackButton="showRebackButton" :companyId="companyId"></callerBlackLists>
            <calleeBlackLists  v-else-if="selectMenuName=='calleeBlackLists'" @rebackEvent="rebackInvoke" :rebackButton="showRebackButton" :companyId="companyId"></calleeBlackLists>
        </div>
        
    </div>
</template>

<script>

import {
    companyInfoList,
}from '@/api/getData';
import ext from "../extension/ext.vue"
import extg from "../extension/extg.vue"
import schedule from "../setting/schedule.vue"
import ivrEntry from "../setting/ivrEntry.vue"
import ivrFlowMenu from "../setting/ivrFlowMenu.vue"
import calleeBlackLists from "../setting/calleeBlackLists.vue"
import callerBlackLists from '../setting/callerBlackLists.vue'

export default {
    data() {
        return {
            //分页
            currentRow: null,
            limit: 10,
            count: 0,
            currentPage: 1,
            searchInput: '', //搜索框
            companyInfoData: [],
            selectmodule:true,
            selectMenuName: '',
            showRebackButton: true,
            companyId: '',
        }
    },
    created() {
       this.getcompanyInfoList(); //加载数据
    },
    components: {
       ext: ext,
       extg: extg,
       schedule: schedule,
       ivrEntry: ivrEntry,
       ivrFlowMenu:ivrFlowMenu,
       calleeBlackLists:calleeBlackLists,
       callerBlackLists:callerBlackLists,
    },
   
    methods: {
        //获取分页 总条数
        async getcompanyInfoList() {
            try {
                let query = {
                    companyname: this.searchInput,
                    staus:'0'
                }
                //分页
                const res = await companyInfoList({
                    pageindex:this.currentPage,
                    pagesize:this.limit,
                    query:JSON.stringify(query)
                });
                if (res.resultCode == '0') {
                    this.companyInfoData = [];
                    //防止后台数据幅值，，前台页面只需要改动此处就好，还有一个好处 就是只选择我需要的数据
                    this.showCheck = res.result.data;
                    this.count=res.result.total;
                    res.result.data.forEach(item => {
                    var istest = '是';
                    if(item.istest == 0){
                        istest = '否';
                    }
                    item.createAt = this.dateFormat(item.createAt);
                    const tableItem = {
                        companyid:item.companyid,
                        contactnumber:item.contactnumber,
                        balance:item.balance,
                        companyname:item.companyname,
                        contactsname:item.contactsname,
                        createAt:item.createAt,
                        description:item.description,
                        email:item.email,
                        istest:istest,
                        updateAt:item.updateAt,
                        uuid:item.uuid,
                    }
                    this.companyInfoData.push(tableItem)
                    })
                } else {
                    throw new Error(res.message)
                }
            } catch (err) {
                console.log('获取数据失败', err);
            }
        },
        handleCurrentChange(val) {
            this.currentPage = val;
            this.getcompanyInfoList();
        },
        handleSelectionChange(val) {
            this.multipleSelection = val;
        },
        handleCompanyInfoSearch() {
            this.getcompanyInfoList();
        },
        handleSizeChange(val) {
        },
        formatter(row, column) {
            return ' -- ' + row.name;
        },
        dateFormat(time) {
            if(time != undefined && time != ''){
                return this.$moment(time).format("YYYY-MM-DD HH:mm:ss");
            }else{
                return '';
            }
        },
        handlejump(index, row,target){
            this.$data.selectmodule = false;
            this.$data.selectMenuName = target;
            this.$data.companyId = row.companyid;
            
        },
        rebackInvoke: function(msg) { //子组件触发父组件事件
            this.$data.selectmodule = true;
           
        },
    }
}
</script>
<style scope lang="less">

