        padding: 0px;
      }
      .cell {
        padding-left: 10px;
      }
      tr {
        border-bottom: 1px solid #e6e6e6;
      }
      thead tr {
        border-bottom: 2px solid #e6e6e6;
      }
    }
    .w-500 {
      .el-dialog {
        width: 500px;
        .el-dialog__body {
          max-height: 500px;
          overflow-y: auto;
          .item{
            height:40px;
