<script>

    import {
        companyInfoList,
        savecompanyInfo,
        deletecompanyInfoByIds,
        importFile,
        exportFile,
    }from '@/api/getData';
    import ElTableTreeColumn from '../base/table-tree/table-column.js';
 
    export default {
        data() {
            var checkNumber = (rule, value, callback) => { //必须填而且必须是数字
                if (value == undefined || "" === value) {
                    callback(new Error('此处不能为空'));
                }
                if (!Number.isInteger(value)) {
                    callback(new Error('请输入数字值'));
                } else {
                    callback();
                }
            };
            var checkNull = (rule, value, callback) => { //要么不填，要填必须是数字
                if (value == undefined || "" === value) {
                    callback();
                }
                if (!Number.isInteger(value)) {
                    callback(new Error('请输入数字值'));
                } else {
                    callback();
                }
            };
            var checkDateVule = (rule, value, callback) => { //必须填入日期
                if (value == undefined || "" === value) {
                    callback(new Error('请输入日期'));
                }else{
                    callback();
                }
            
            };
            var checkNumberOrChar = (rule, value, callback) => { //非空且只能数字或者英文
                if (value == undefined || "" === value) {
                    callback(new Error('请输入5位数字或英文'));
                }else{
                    var reg = new RegExp("^[A-Za-z0-9]+$");
                    if(!reg.test(value)){
                        callback(new Error('只能输入数字或英文'));
                    }else{
                        if(value.length == 5){
                            callback();
                        }else{
                            callback(new Error('长度必须为5位'));
                        }
                    }
                }
            };
            return {
                //分页
                currentRow: null,
                limit: 10,
                count: 0,
                currentPage: 1,
                searchInput: '', //搜索框
                statedetail: '',
                companyInfoData: [],
                showCheck: {}, //查看对象
                dialogFormCheck: false, //查看弹窗
                multipleSelection: [],
                //弹窗
                dialogFormVisible: false,
                dialogFormEdit: false, //编辑弹窗
                companyInfoForm: {
                    companyname: '',
                    contactsname: '',
                    contactnumber: '',
                    email: '',
                    balance: '',
                    istest: '',
                    description: '',
                    address: '',
                    website: '',
                    license: '',
                    password: '',
                    prefix: '',
                    expirationAt:'',
                    username:'',
                    externalNumber: '',
                    extNumber: 0,
                    extgNumber: 0,
                    ivrState: 0,
                    recordNumber: 0,
                    rootNumber: '0',
                    
                },
                companyInfoEditForm: {
                    companyname: '',
                    contactsname: '',
                    contactnumber: '',
                    email: '',
                    balance: '',
                    istest: '',
                    description: '',
                    address: '',
                    website: '',
                    license: '',
                    password: '',
                    prefix: '',
                    expirationAt:'',
                    username:'',
                    extNumber: 0,
                    extgNumber: 0,
                    ivrState: 0,
                    recordNumber: 0,
                    rootNumber: '0',
                    externalNumber: '',
                    
                },
                istextItems: [{
                value: 1,
                label: '是'
                },{
                value: 0,
                label: '否'
                }],
                formLabelWidth: '80px',
                addformRules: {
                    istest: [{validator: checkNull, trigger: 'blur'}],
                    extNumber: [{required: true,validator: checkNumber, trigger: 'blur'}],
                    extgNumber: [{required: true,validator: checkNumber, trigger: 'blur'}],
                    recordNumber: [{required: true,validator: checkNumber, trigger: 'blur'}],
                    expirationAt:[{required:true,validator: checkDateVule, trigger:'blur'}],
                    password : [{required: true, message:'请输入登陆密码',trigger: 'blur'}],
                    username : [{required: true, message:'请输入用户登陆名称',trigger: 'blur'}],
                    companyname : [{required: true, message:'请输入租户名称',trigger: 'blur'}],
                    contactsname: [{required: true, message:'请输入联系人',trigger: 'blur'}],
                    contactnumber: [{required: true, message:'请输入联系方式',trigger: 'blur'}],
                    prefix: [{validator: checkNumberOrChar,trigger: 'blur'}],
                    
                },
                formRules: {
                    istest: [{validator: checkNull, trigger: 'blur'}],
                    extNumber: [{required: true,validator: checkNumber, trigger: 'blur'}],
                    extgNumber: [{required: true,validator: checkNumber, trigger: 'blur'}],
                    recordNumber: [{required: true,validator: checkNumber, trigger: 'blur'}],
                    expirationAt:[{required:true,validator: checkDateVule, trigger:'blur'}],
                    companyname : [{required: true, message:'请输入租户名称',trigger: 'blur'}],
                    contactsname: [{required: true, message:'请输入联系人',trigger: 'blur'}],
                    contactnumber: [{required: true, message:'请输入联系方式',trigger: 'blur'}],
                    prefix: [{validator: checkNumberOrChar,trigger: 'blur'}],
                    
                },
                //文件上传部分
                importFile: importFile,
                uploaddata:{type:'other'},
                filenumber: 0,
                uploadfileList: [],
                ivrStateInfo:[{
                    label: '开通',
                    value: 0,
                    },{
                    label: '停用',
                    value: 1,
                }],
                dialogTip: false,
                tip:{
                    url:window.location.href.split('/#')[0]+"/#/login", 
                    account:'',
                    password:'',
                },
                exportFile: exportFile,
                statusinfo:[{
                    label: '全部状态',
                    value: '',
                    },{
                    label: '已停用',
                    value: '1',
                    },{
                    label: '使用中',
                    value: '0',
                }],
                showmodle: false,
            }
        },
        created() {
            this.getcompanyInfoList(); //加载数据
        },
        mounted(){
           // this.$data.dialogFormVisible=false;
        },
        components: {
            ElTableTreeColumn
        },
        methods: {
        //获取分页 总条数
            async getcompanyInfoList() {
                try {
                    let query = {
                        companyname: this.searchInput,
                        staus:this.statedetail,
                    }
                    //分页
                    const res = await companyInfoList({
                        pageindex:this.currentPage,
                        pagesize:this.limit,
                        query:JSON.stringify(query)
                    });
                    if (res.resultCode == '0') {
                        this.companyInfoData = [];
                        //防止后台数据幅值，，前台页面只需要改动此处就好，还有一个好处 就是只选择我需要的数据
                        this.showCheck = res.result.data;
                        this.count=res.result.total;
                        res.result.data.forEach(item => {
                        var status = '使用中';
                        if(item.status == '1'){
                            status = '已停用';
                        }
                        item.createAt = this.dateFormat(item.createAt);
                        var extraNumbercount = 0;
                        if(item.externalNumber != undefined){
                             extraNumbercount = item.limit.externalNumber.split(',').length;
                        }
                        const tableItem = {
                            companyid:item.companyid,
                            contactnumber:item.contactnumber,
                            balance:item.balance,
                            companyname:item.companyname,
                            contactsname:item.contactsname,
                            createAt:item.createAt,
                            description:item.description,
                            email:item.email,
                            //istest:istest,
                            updateAt:item.updateAt,
                            uuid:item.uuid,
                            address: item.address,
                            website: item.website,
                            license: item.license,
                            password: item.password,
                            prefix: item.prefix,
                            status: status,
                            expirationAt:item.expirationAt,
                            username:item.username,
                            extNumber: item.limit.extNumber,
                            extgNumber: item.limit.extgNumber,
                            ivrState: item.limit.ivrState,
                            recordNumber: item.limit.recordNumber,
                            rootNumber: item.limit.rootNumber,
                            externalNumber: item.limit.externalNumber,
                            extraNumbercount:extraNumbercount,
                        }
                        this.companyInfoData.push(tableItem)
                        })
                    } else {
                        throw new Error(res.message)
                    }
                } catch (err) {
                    console.log('获取数据失败', err);
                }
            },
            handSaveParam(args){
                if(args.istest == undefined || args.istest === '')delete args.istest;
                if(args.extNumber == undefined || args.extNumber === '')delete args.extNumber;
                if(args.extgNumber == undefined || args.extgNumber === '')delete args.extgNumber;
                if(args.recordNumber == undefined || args.recordNumber === '')delete args.recordNumber;
                args.expirationAt = this.dateFormat(args.expirationAt);
                return args;
            },
            handleSizeChange(val) {
            },
            handleCurrentChange(val) {
                this.currentPage = val;
                this.getcompanyInfoList();
            },
            handleSelectionChange(val) {
                this.multipleSelection = val;
            },
            handleCompanyInfoSearch() {
                this.getcompanyInfoList();
            },
            formatter(row, column) {
                return ' -- ' + row.name;
            },
            dateFormat(time) {
                if(time != undefined && time != ''){
                    return this.$moment(time).format("YYYY-MM-DD HH:mm:ss");
                }else{
                    return '';
                }
            },
            //增加
            handleCompanyInfoAdd() {
                this.companyInfoForm = {
                companyname: '',
                contactsname: '',
                contactnumber: '',
                email: '',
                balance: '',
                istest: '',
                description: '',
                address: '',
                website: '',
                license: '',
                password: '',
                prefix:'',
               
                username:'',
                extNumber: 0,
                extgNumber: 0,
                ivrState: 0,
                recordNumber: 0,
                rootNumber: '0',
                externalNumber: '',
                   
                };
                this.tip.account='';
                this.tip.password='';
                this.uploadfileList = new Array();
                this.$data.filenumber = 0;
                this.dialogFormVisible = true;
                this.clearFiles();
            },
            //关闭弹框时重置表单
            handleDialogWithFormClose() {
                for (var prop in this.$refs) {
                if (this.$refs.hasOwnProperty(prop)) {
                    if (this.$refs[prop].$vnode.tag == 'vue-component-48-ElForm') {
                    this.$refs[prop].resetFields();
                    }
                }
                }
            },
            //批量删除
            handleBatchDelete() {
                let that = this;
                if (this.multipleSelection.length === 0) {
                this.$alert('请至少选择一条记录', '提示', {
                    confirmButtonText: '确定',
                    callback: () => {
                    return false;
                    }
                });
                } else {
                this.$confirm('此操作将永久删除记录, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(async() => {
                    try {
                    let deletedIdArr = [];
                    that.multipleSelection.forEach(s => {
                        deletedIdArr.push(s.uuid)
                    })
                    let deletedIdsString = deletedIdArr.join(',');
                    let data = {
                        uuid: deletedIdsString
                    }
                    //此方法可以和后台协商一致，单条与批量删除使用同一个函数
                    const result = await deletecompanyInfoByIds(data);
                    if (result.resultCode == '0') {
                        that.getcompanyInfoList();
                        this.$message({
                        type: 'success',
                        message: '删除成功!'
                        });
                    }
                    } catch (err) {
                    console.log(err)
                    }
                }).catch(() => {
                    this.$message({
                    type: 'info',
                    message: '已取消删除'
                    });
                });
                }
            },
            //删除当前记录
            handleCompanyInfoDelete(index, row) {
                let that = this;
                this.$confirm('此操作将删除此租户下所有分机, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
                }).then(async() => {
                try {
                    let data = {
                    uuid: row.uuid,
                    companyid:row.companyid
                    }
                    //此方法可以和后台协商一致，单条与批量删除使用同一个函数
                    const result = await deletecompanyInfoByIds(data);
                    if (result.resultCode == '0') {
                    that.getcompanyInfoList();
                    this.$message({
                        type: 'success',
                        message: '删除成功!'
                    });
                    this.$store.baseStore.dispatch('getAllDeptId');
                    }
                } catch (err) {
                    console.log(err)
                }
                }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消删除'
                });
                });
            },
            //编辑
            handleCompanyInfoEdit(index, row) {
                if (typeof row !== 'undefined') {
                    this.companyInfoEditForm.companyname = row.companyname;
                    this.companyInfoEditForm.contactsname = row.contactsname;
                    this.companyInfoEditForm.contactnumber = row.contactnumber;
                    this.companyInfoEditForm.companyid = row.companyid;
                    this.companyInfoEditForm.email = row.email;
                    this.companyInfoEditForm.balance = row.balance;
                    this.companyInfoEditForm.istest = row.istest;
                    this.companyInfoEditForm.description = row.description;
                    this.companyInfoEditForm.uuid = row.uuid;
                    this.companyInfoEditForm.address = row.address;
                    this.companyInfoEditForm.website = row.website;
                    this.companyInfoEditForm.license = row.license;
                    this.companyInfoEditForm.password = '';
                    this.companyInfoEditForm.username = '';
                    this.companyInfoEditForm.prefix = row.prefix;
                    this.companyInfoEditForm.extNumber = row.extNumber;
                    this.companyInfoEditForm.extgNumber = row.extgNumber;
                    this.companyInfoEditForm.ivrState = row.ivrState;
                    this.companyInfoEditForm.recordNumber = row.recordNumber;
                    this.companyInfoEditForm.rootNumber = row.rootNumber;
                    this.companyInfoEditForm.externalNumber = row.externalNumber;
                    this.companyInfoEditForm.expirationAt = row.expirationAt;
                }
                this.dialogFormEdit = true;
            },
            //查看
            handlecompanyInfoCheck(index, row) {
                this.dialogFormCheck = true;
                row.createAt=this.dateFormat(row.createAt);
                row.updateAt=this.dateFormat(row.updateAt);
                row.expirationAt = this.dateFormat(row.expirationAt);
                this.$data.showCheck = row;
            },
            //添加 编辑的 保存
            handleCompanyInfoSave(formName) {
                let that = this;
                this.$refs[formName].validate(async(valid) => {
                if (valid) {
                    try {
                        //数据拿出来，如果为空则去掉
                        var args = this.$refs[formName].model;
                        var params = this.handSaveParam(args);
                        if(formName == 'companyInfoForm'){//添加
                            
                            var arr = this.$data.uploadfileList;
                            var paths = "";
                            for(var i = 0; i < arr.length; i++){
                                var path = arr[i].response.result.split("=")[1];
                                if(i == 0){
                                    paths = path;
                                }else{
                                    paths = paths + "," + path;
                                }
                            }
                            params.license = paths;
                            params.balance = 0;
                        }
                        const result = await savecompanyInfo(params);
                        if (result.resultCode == '0') {
                            if(formName == 'companyInfoForm'){//添加
                                that.dialogFormVisible = false;
                                this.tip.account = params.username;
                                this.tip.password = params.password;
                                this.dialogTip = true;
                            }else{
                                this.dialogFormEdit = false;
                                this.$message({
                                type: 'success',
                                message: '租户信息修改成功'
                                });
                            }
                            that.getcompanyInfoList();
                            this.$store.baseStore.dispatch('getAllDeptId');
                        }else if(result.resultCode == '00009'){
                            this.$message({
                                type: 'error',
                                message: result.result,
                            });
                        }else if(result.resultCode == '91002'){
                            this.$message({
                                type: 'error',
                                message: result.result,
                            });
                        }
                    } catch (err) {
                    console.log(err)
                    this.$message({
                        type: 'error',
                        message: '保存失败'
                    });
                    }
                } else {
                    this.$message({
                        type: 'info',
                        message: '请检查输入是否不合法'
                    });
                    return false;
                }
                });
            },

            beforeUpload(file){
                var type = file.type;
                if(type.indexOf("image")==-1){
                    this.$message({
                        type: 'info',
                        message: '只能上传图片！'
                    });
                    return false;
                }
                if(file.size>10485760){
                    this.$message({
                        type: 'info',
                        message: '图片最大为10MB！'
                    });
                    return false;
                }

                if(this.filenumber > 2){
                    this.$message({
                        type: 'info',
                        message: '最多同时上传3个文件！'
                    });
                    return false;
                }
                return true;
            },
            handleRemove(file, fileList) {
                var tmp = this.filenumber;
                if(file.response != undefined && tmp <= 3){
                    this.filenumber = tmp - 1;
                }
            },
            onsucess(response,file, fileList){
                var tmp = this.filenumber;
                this.filenumber = tmp + 1;
                this.$data.uploadfileList = fileList;
            },
            clearFiles() { //清空
              if(this.$refs.xupload != undefined){
                this.$refs.xupload.clearFiles();
              }
            },
            exportCompany(){
                window.location.href = this.$data.exportFile;
            },
        }
    }

</script>
<style scope lang="less">

  .globlgl {
    padding: 33px 0 0 26px;
    box-sizing: border-box;
    .float-l {
      float: left;
    }
    .float-r {
      float: right;
    }
    .m-t-25 {
      margin-top: 25px;
    }
    //table 改组建
    .el-table {
      width: 100%;
      height: 100%;
      border: 0;
      &:before,
      &:after {
        display: none;
      }
      .el-table__body-wrapper {
        overflow: hidden;
      }
      th,
      td,
      .cell {
        border: 0px;
        background-color: initial;
        padding: 0px;
      }
      .cell {
        padding-left: 10px;
      }
      tr {
        border-bottom: 1px solid #e6e6e6;
      }
      thead tr {
        border-bottom: 2px solid #e6e6e6;
      }
    }
    .w-500 {
      .el-dialog {
        width: 500px;
        .el-dialog__body {
          max-height: 500px;
          overflow-y: auto;
          .item{
            height:40px;
            line-height:40px;
          }
        }
      }
    }
    .w-1000 {
      .el-dialog {
        width: 1000px;
        .el-dialog__body {
          max-height: 500px;
          overflow-y: auto;
        }
      }
    }
    .el-button--text {
      color: #00a8d7;
    }
    .btns {
      .btn {
        width: 80px;
        height: 36px;
        border: 1px solid #e6e6e6;
        color: #454545;
      }
      .float-r {
        float: right;
      }
    }
    .tables {
      .table {
        border: 0;
        margin: 15px 0;
      }
    }
    .m-t-26 {
      margin-top: 26px;
    }
    .globlgl-type {
      margin-right: 20px;
    }
    .nav {
      color: #99a9bf;
      cursor: pointer;
      transition: .3s;
      vertical-align: baseline;
      margin-right: 30px;
      &:hover {
        color: #48576a;
      }
    }
    .tree-grid {
      margin: 15px 0;
    }
  }

</style>
<style lang="less">
    .globlgl.globlgl--custom .el-table .el-table__body-wrapper {
        overflow: auto;
    }
</style>




