<template>
    <div class="extGroupsgl">
        <div class="btns">
            <el-button class="float-l btn" @click="rebackEvent" v-show="rebackButton">返回</el-button>
            <el-button class="float-l btn" @click="handleExtGroupsAdd">添加</el-button>
            <el-button class="btn" @click="handleBatchDelete">批量删除</el-button>
            <!-- <div class="search float-r">
                <el-input class="search-input" placeholder="分机号码" icon="search" v-model="searchInput" :on-icon-click="handleExtGroupsSearch">
                </el-input>
            </div> -->
        </div>
        <div class="tables">
        <el-table class="table" ref="multipleTable" :data="extGroupsData" border tooltip-effect="dark" @selection-change="handleSelectionChange">
            <el-table-column type="selection" width="55"></el-table-column>
            <el-table-column prop="vlanId" label="租户号">
                <template slot-scope="scope">
                    <el-button @click="handleextGroupsCheck(scope.$index, scope.row)" type="text">{{scope.row.vlanId}}</el-button>
                </template>
            </el-table-column>
            <el-table-column prop="groupId" label="分机组ID"></el-table-column>
            <!-- <el-table-column prop="extNbr" label="分机号"></el-table-column> -->
            <el-table-column prop="extNumber" label="分机数量"></el-table-column>
            <!-- <el-table-column prop="priority" label="优先级"></el-table-column> -->
            <el-table-column prop="rules" label="振铃规则"></el-table-column>
            <el-table-column prop="description" label="备注"></el-table-column>
            <el-table-column label="操作" width="120">
                <template slot-scope="scope">
                    <el-button type="text" size="small" @click="handleExtGroupsDelete(scope.$index, scope.row)">删除</el-button>
                    <el-button type="text" size="small" @click="handleExtGroupsEdit(scope.$index, scope.row)">编辑
                    </el-button>
                </template>
            </el-table-column>
        </el-table>
        <!--添加分机组-->
        <el-dialog title="添加分机组" class="w-500" :visible.sync="dialogFormVisible" @close="handleDialogWithFormClose()" :close-on-click-modal="2==1">
            <el-form :model="extGroupsForm" :rules="formRules" labelPosition="left" ref="extGroupsForm">
                <el-form-item prop="vlanId" label="租户名称" :label-width="formLabelWidth" v-if="this.$store.baseStore.getters.getUserInfo.managerLevel == '999'">
                    <el-select v-model.number="extGroupsForm.vlanId" placeholder="请选择">
                        <el-option  v-for="item in allCompanyId" :key="item.value" :label="item.label" :value="item.value">
                        </el-option>
                    </el-select> 
                </el-form-item>
                <el-form-item prop="groupId" label="分机组ID" :label-width="formLabelWidth">
                    <el-input v-model.number="extGroupsForm.groupId" auto-complete="off"></el-input>
                </el-form-item>
                <!-- <el-form-item prop="extNbr" label="分机号" :label-width="formLabelWidth">
                    <el-input v-model="extGroupsForm.extNbr" auto-complete="off"></el-input>
                </el-form-item> -->
                <!-- <el-form-item prop="priority" label="优先级" :label-width="formLabelWidth">
                    <el-input v-model.number="extGroupsForm.priority" auto-complete="off"></el-input>
                </el-form-item> -->
                <el-form-item prop="rules" label="振铃规则" :label-width="formLabelWidth">
                    <el-select v-model.number="extGroupsForm.rules" placeholder="请选择">
                        <el-option  v-for="item in rulesTypeArr" :key="item.value" :label="item.label" :value="item.value">
                        </el-option>
                    </el-select> 
                </el-form-item>
                <el-form-item prop="description" label="备注" :label-width="formLabelWidth">
                    <el-input v-model="extGroupsForm.description"  :maxlength="255" auto-complete="off"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
            <el-button @click="dialogFormVisible = false">取 消</el-button>
            <el-button type="primary" @click="handleExtGroupsSave('extGroupsForm')">确 定</el-button>
            </div>
        </el-dialog>
        <!--编辑分机组-->
        <el-dialog title="编辑分机组" class="w-500" :visible.sync="dialogFormEdit" @close="handleDialogWithFormClose()" :close-on-click-modal="2==1">
            <el-form :model="extGroupsEditForm" :rules="formRules" labelPosition="left" ref="extGroupsEditForm">
                <el-form-item prop="vlanId" label="租户名称" :label-width="formLabelWidth" v-if="this.$store.baseStore.getters.getUserInfo.managerLevel == '999'">
                    <el-select v-model.number="extGroupsEditForm.vlanId" placeholder="请选择">
                        <el-option  v-for="item in allCompanyId" :key="item.value" :label="item.label" :value="item.value">
                        </el-option>
                    </el-select> 
                </el-form-item>
                <el-form-item prop="groupId" label="分机组ID" :label-width="formLabelWidth">
                    <el-input v-model.number="extGroupsEditForm.groupId" :disabled="true" auto-complete="off"></el-input>
                    
                </el-form-item>
                <!-- <el-form-item prop="extNbr" label="分机号" :label-width="formLabelWidth">
                    <el-input v-model="extGroupsEditForm.extNbr" auto-complete="off"></el-input>
                    
                </el-form-item> -->
                <!-- <el-form-item prop="priority" label="优先级" :label-width="formLabelWidth">
                    <el-input v-model.number="extGroupsEditForm.priority" auto-complete="off"></el-input>
                    
                </el-form-item> -->
                <el-form-item prop="rules" label="振铃规则" :label-width="formLabelWidth">
                    <el-select v-model.number="extGroupsEditForm.rules" placeholder="请选择">
                        <el-option  v-for="item in rulesTypeArr" :key="item.value" :label="item.label" :value="item.value">
                        </el-option>
                    </el-select> 
                </el-form-item>
                <el-form-item prop="description" label="备注" :label-width="formLabelWidth">
                    <el-input v-model="extGroupsEditForm.description" :maxlength="255" auto-complete="off"></el-input>
                    
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
            <el-button @click="dialogFormEdit = false">取 消</el-button>
            <el-button type="primary" @click="handleExtGroupsSave('extGroupsEditForm')">确 定</el-button>
            </div>
        </el-dialog>
        <!--查看弹窗-->
        <el-dialog title="查看分机组" class="w-500" :visible.sync="dialogFormCheck" :close-on-click-modal="2==1">
            <div class="item">租户号: {{showCheck.vlanId}}</div>
            <!-- <div class="item">分机号: {{showCheck.extNbr}}</div> -->
            <div class="item">分机数量: {{showCheck.extNumber}}</div>
            <div class="item">分机组ID: {{showCheck.groupId}}</div>
            <!-- <div class="item">优先级: {{showCheck.priority}}</div> -->
            <div class="item">振铃规则: {{showCheck.rules}}</div>
            <div class="item">备注: {{showCheck.description}}</div>
            <div class="item">创建时间: {{showCheck.createAt}}</div>
            <div class="item">更新时间: {{showCheck.updateAt}}</div>
            <div slot="footer" class="dialog-footer">
            <el-button @click="dialogFormCheck = false">取 消</el-button>
            <el-button type="primary" @click="dialogFormCheck=false">确 定</el-button>
            </div>
        </el-dialog>
        </div>
        <div class="float-r Pagination" style="text-align: left;margin-top: 10px;">
        <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage" :page-size="10" layout="total, prev, pager, next" :total="count">
        </el-pagination>
        </div>
    </div>
</template>

<script>

  import {
    extGroupsList,
    saveextGroups,
    deleteextGroupsByIds,
    getExtgCountByVlanId,
  }
    from '@/api/getData';
  import ElTableTreeColumn from '../base/table-tree/table-column.js';
  
  export default {
    data() {
      var checkNumber = (rule, value, callback) => { //必须填而且必须是数字
            if (value == undefined || "" === value) {
                callback(new Error('此处不能为空'));
            }
            if (!Number.isInteger(value)) {
                callback(new Error('请输入数字值'));
            } else {
                callback();
            }
        };
        var checkNull = (rule, value, callback) => { //要么不填，要填必须是数字
            if (value == undefined || "" === value) {
                callback();
            }
            if (!Number.isInteger(value)) {
                callback(new Error('请输入数字值'));
            } else {
                callback();
            }
      };
      return {
        //分页
        currentRow: null,
        limit: 10,
        count: 0,
        currentPage: 1,
        searchInput: '', //搜索框
        extGroupsData: [],
        showCheck: {}, //查看对象
        dialogFormCheck: false, //查看弹窗

        multipleSelection: [],
        //弹窗
        dialogFormVisible: false,
        dialogFormEdit: false, //编辑弹窗
        extGroupsForm: {
          vlanId: '',
          groupId: '',
          // extNbr: '',
          // priority: '',
          rules: '',
          description: '',
        },
        extGroupsEditForm: {
             vlanId: '',
             groupId: '',
            //  extNbr: '',
            //  priority: '',
             rules: '',
             description: '',
        },
        formLabelWidth: '80px',
        formRules: {
           vlanId: [{required: true,  validator: checkNumber, message:'请输入租户号(数字)', trigger: 'blur'}],
           groupId: [{required: true,  validator: checkNumber, trigger: 'blur'}],
          //  priority: [{validator: checkNull, trigger: 'blur'}],
           rules: [{validator: checkNull, trigger: 'blur'}],
         
        },
        rulesTypeArr: [{
          value: 0,
          label: '同振'
        },{
          value: 1,
          label: '顺振'
        }],
        currentUserVlanId:this.$store.baseStore.getters.getUserInfo.vlanId,
       
      }
    },
    created() {
      this.getextGroupsList(); //加载数据
    },
    mounted(){
        var coms = this.$store.baseStore.getters.getAllCompanyInfo;
        if(coms == undefined || coms.length == 0){
            this.$store.baseStore.dispatch('getAllDeptId');
        }else{
            this.$data.allCompanyId= coms;
        }
    },
    watch:{
        companyId: function (val, oldVal){
            this.getextGroupsList(); 
        },
    },
    computed: {
        allCompanyId:function() {
             return this.$store.baseStore.getters.getAllCompanyInfo;
        }
    },
    components: {
      ElTableTreeColumn
    },
    props: {
        rebackButton: false,
        companyId: '',
    },
    methods: {
      //获取分页 总条数
      async getextGroupsList() {
        try {
          let query = {
            // extNbr: this.searchInput
            admincompanyId:this.companyId,
          }
          //分页
          const res = await extGroupsList({
            pageindex:this.currentPage,
            pagesize:this.limit,
            query:JSON.stringify(query)
          });
          if (res.resultCode == '0') {
            this.extGroupsData = [];
            //防止后台数据幅值，，前台页面只需要改动此处就好，还有一个好处 就是只选择我需要的数据
            this.showCheck = res.result.data;
            this.count=res.result.total;
            res.result.data.forEach(item => {
              var rules = '同振';
              if(item.rules==1){
                rules = '顺振';
              }
              const tableItem = {
                createAt:item.createAt,
                description:item.description,
                // extNbr:item.extNbr,
                extNumber:item.extNumber,
                groupId:item.groupId,
                // priority:item.priority,
                rules:rules,
                updateAt:item.updateAt,
                uuid:item.uuid,
                vlanId:item.vlanId,
              }
              this.extGroupsData.push(tableItem)
            })
          } else {
            throw new Error(res.message)
          }
        } catch (err) {
          console.log('获取数据失败', err);
        }
      },
      handSaveParam(args){
         if(args.groupId == undefined || args.groupId === '')delete args.groupId;
        //  if(args.priority == undefined || args.priority === '')delete args.priority;
         if(args.rules == undefined || args.rules === '')delete args.rules;
         return args;
      },
      handleSizeChange(val) {
      },
      handleCurrentChange(val) {
        this.currentPage = val;
        this.getextGroupsList();
      },
      handleSelectionChange(val) {
        this.multipleSelection = val;
      },
      handleExtGroupsSearch() {
        this.getextGroupsList();
      },
      formatter(row, column) {
        return ' -- ' + row.name;
      },
      dataFormat(row, column) {
         var time =  row[column.property];
         if(time != undefined && time != ''){
            return moment(time).format("YYYY-MM-DD HH:mm:ss")  
         }else{
            return '';
         }
         
      },
      //增加
      handleExtGroupsAdd() {
        var tempVlanId = this.$data.currentUserVlanId;
        if(this.companyId != undefined && this.companyId != ''){
            tempVlanId = parseInt(this.companyId);
        }
        this.extGroupsForm = {
          vlanId: tempVlanId,
          groupId: '',
          // extNbr: '',
          // priority: 100,
          rules: 0,
          description: '',
        };
        this.dialogFormVisible = true;
      },
      //关闭弹框时重置表单
      handleDialogWithFormClose() {
        for (var prop in this.$refs) {
          if (this.$refs.hasOwnProperty(prop)) {
            if (this.$refs[prop].$vnode.tag == 'vue-component-48-ElForm') {
              this.$refs[prop].resetFields();
            }
          }
        }
      },
      //批量删除
      handleBatchDelete() {
        let that = this;
        if (this.multipleSelection.length === 0) {
          this.$alert('请至少选择一条记录', '提示', {
            confirmButtonText: '确定',
            callback: () => {
              return false;
            }
          });
        } else {
          this.$confirm('此操作将永久删除记录, 是否继续?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(async() => {
            try {
              let deletedIdArr = [];
              that.multipleSelection.forEach(s => {
                deletedIdArr.push(s.uuid)
              })
              let deletedIdsString = deletedIdArr.join(',');
              let data = {
                uuid: deletedIdsString
              }
              //此方法可以和后台协商一致，单条与批量删除使用同一个函数
              const result = await deleteextGroupsByIds(data);
              if (result.resultCode == '0') {
                that.getextGroupsList();
                this.$message({
                  type: 'success',
                  message: '删除成功!'
                });
              } else if(result.resultCode == '91007'){
                this.$message({
                  type: 'error',
                  message: result.result,
                });
              }
            } catch (err) {
              console.log(err)
            }
          }).catch(() => {
            this.$message({
              type: 'info',
              message: '已取消删除'
            });
          });
        }
      },
      //删除当前记录
      handleExtGroupsDelete(index, row) {
        let that = this;
        this.$confirm('此操作将永久删除记录, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(async() => {
          try {
            let deletedIdsString = [row.uuid].join(',');
            let data = {
              uuid: deletedIdsString
            }
            //此方法可以和后台协商一致，单条与批量删除使用同一个函数
            const result = await deleteextGroupsByIds(data);
            if (result.resultCode == '0') {
              that.getextGroupsList();
              this.$message({
                type: 'success',
                message: '删除成功!'
              });
            } else if(result.resultCode == '91007'){
                this.$message({
                  type: 'error',
                  message: result.result,
                });
              }
          } catch (err) {
            console.log(err)
          }
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });
      },
      //编辑
      handleExtGroupsEdit(index, row) {
        let rules = 0;
        if(row.rules == '顺振'){
          rules = 1;
        }
        if (typeof row !== 'undefined') {
            this.extGroupsEditForm.vlanId = row.vlanId;
            this.extGroupsEditForm.groupId = row.groupId;
            // this.extGroupsEditForm.extNbr = row.extNbr;
            // this.extGroupsEditForm.priority = row.priority;
            this.extGroupsEditForm.rules = rules;
            this.extGroupsEditForm.description = row.description;
            this.extGroupsEditForm.uuid = row.uuid;
        }
        this.dialogFormEdit = true;
      },
      //查看
      handleextGroupsCheck(index, row) {
        this.dialogFormCheck = true;
        row.createAt=this.dateFormat(row.createAt);
        row.updateAt=this.dateFormat(row.updateAt);
        this.$data.showCheck = row;
      },
      dateFormat(time) {
         if(time != undefined && time != ''){
            return this.$moment(time).format("YYYY-MM-DD HH:mm:ss");
         }else{
            return '';
         }
         
      },
      //添加 编辑的 保存
      handleExtGroupsSave(formName) {
        let that = this;
        this.$refs[formName].validate(async(valid) => {
          if (valid) {
            try {
              //数据拿出来，如果为空则去掉
              var args = this.$refs[formName].model;
              var vlanID = this.$store.baseStore.getters.getUserInfo.vlanId;
              if(this.$store.baseStore.getters.getUserInfo.managerLevel=='999'){
                vlanID = args.vlanId;
              }
              var params = this.handSaveParam(args);
              var extCount = await getExtgCountByVlanId({
                vlanId:vlanID,
              });
              if(extCount.result == -1 && formName == 'extGroupsForm'){
                this.$message({
                     type: 'error',
                     message: '租户不存在'
                });
              } else if(extCount.result > 0 || formName == 'extGroupsEditForm'){
                const result = await saveextGroups(params);
                if (result.resultCode == '0') {
                    if(formName == 'extGroupsForm'){//添加
                          that.dialogFormVisible = false;
                    }else{
                      this.dialogFormEdit = false;
                    }
                    that.getextGroupsList();
                    this.$message({
                      type: 'success',
                      message: '分机组保存成功'
                    });
                }else if(result.resultCode == '00009'){
                    this.$message({
                        type: 'error',
                        message: result.result,
                    });
                }else{
                    this.$message({
                      type: 'error',
                      message: '保存失败'
                    });
                }
              } else {
                this.$message({
                  type: 'error',
                  message: '当前租户下分机组数量已达上限'
                });
              }
              
            } catch (err) {
               this.$message({
                  type: 'error',
                  message: '保存失败'
               });
            }
          } else {
             this.$message({
                type: 'info',
                message: '请检查输入是否不合法'
             });
            return false;
          }
        });
      },
      rebackEvent(){
          var args =  new Object();
          this.$emit('rebackEvent',args);
      },
    }
  }

</script>
<style scope lang="less">

  .extGroupsgl {
    padding: 33px 0 0 26px;
    box-sizing: border-box;
    .float-l {
      float: left;
    }
    .float-r {
      float: right;
    }
    .m-t-25 {
      margin-top: 25px;
    }
    //table 改组建
    .el-table {
      width: 100%;
      height: 100%;
      border: 0;
      &:before,
      &:after {
        display: none;
      }
      .el-table__body-wrapper {
        overflow: hidden;
      }
      th,
      td,
      .cell {
        border: 0px;
        background-color: initial;
        padding: 0px;
      }
      .cell {
        padding-left: 10px;
      }
      tr {
        border-bottom: 1px solid #e6e6e6;
      }
      thead tr {
        border-bottom: 2px solid #e6e6e6;
      }
    }
    .w-500 {
      .el-dialog {
        width: 500px;
        .el-dialog__body {
          max-height: 500px;
          overflow-y: auto;
          .item{
            height:40px;
            line-height:40px;
          }
        }
      }
    }
    .w-1000 {
      .el-dialog {
        width: 1000px;
        .el-dialog__body {
          max-height: 500px;
          overflow-y: auto;
        }
      }
    }
    .el-button--text {
      color: #00a8d7;
    }
    .btns {
      .btn {
        width: 80px;
        height: 36px;
        border: 1px solid #e6e6e6;
        color: #454545;
      }
      .float-r {
        float: right;
      }
    }
    .tables {
      .table {
        border: 0;
        margin: 15px 0;
      }
    }
    .m-t-26 {
      margin-top: 26px;
    }
    .extGroupsgl-type {
      margin-right: 20px;
    }
    .nav {
      color: #99a9bf;
      cursor: pointer;
      transition: .3s;
      vertical-align: baseline;
      margin-right: 30px;
      &:hover {
        color: #48576a;
      }
    }
    .tree-grid {
      margin: 15px 0;
    }
  }

</style>



