<script>
  import {
    extensionsList,
    saveextensions,
    batchsaveextensions,
    deleteextensionsByIds,
    selectExtGroupByVlanId,
    selectAllFailedFlowId,
    extensionsState,
    resetExtPasswordByExtNbr,
    getExtCountByVlanId,
    canSelectRecord,
    getExtNbrPrefix,
    getNotOccuptNumber,
  }
  from '@/api/getData';
  import ElTableTreeColumn from '../base/table-tree/table-column.js';
  
  export default {
    data() {
        var checkNumber = (rule, value, callback) => { //必须填而且必须是数字
            if (value == undefined || "" === value) {
                callback(new Error('此处不能为空'));
            }
            if (!Number.isInteger(value)) {
                callback(new Error('请输入数字值'));
            } else if(value>1000 || value<0) {
                callback(new Error('最小为0,最大为1000'));
            } else {
                callback();
            }
        };
        var checkNum = (rule, value, callback) => { //必须填而且必须是数字
            if (value == undefined || "" === value) {
                callback(new Error('此处不能为空'));
            }
            if (!Number.isInteger(value)) {
                callback(new Error('请输入数字值'));
            } else {
                callback();
            }
        };
        var checkNull = (rule, value, callback) => { //要么不填，要填必须是数字
            if (value == undefined || "" === value) {
                callback();
            }
            if (!Number.isInteger(value)) {
                callback(new Error('请输入数字值'));
            } else {
                callback();
            }
        };
        var check = (rule, value, callback) => { //要么不填，要填必须是数字
            if (value == undefined || "" === value) {
                callback();
            }
            if (value != ' ' && !Number.isInteger(value)) {
                callback(new Error('请输入数字值'));
            } else {
                callback();
            }
        };
      return {
        //分页
        currentRow: null,
        limit: 10,
        count: 0,
        currentPage: 1,
        searchInput: '', //搜索框
        groupIdInput:'',//分机组id搜索框
        extensionsData: [],
        showCheck: {}, //查看对象
        dialogFormCheck: false, //查看弹窗
        dialogResetPassword:false,
        multipleSelection: [],
        //弹窗
        dialogFormVisible: false,
        dialogBatchFormVisible: false,
        dialogFormEdit: false, //编辑弹窗
        cantSelectRecord: false,//当前录音分机数量达上限则为true，即禁用录音选择框
        restFrom:{
          resetPassword:'',
        },
        extensionsForm: {
          vlanId: 0,
          extName: '',
          extNbr: '',
          password: '',
          domain: '',
          didNumber: '',
          groupId: -1,
          callFailedFlowId: '',
          enable: '',
          enableRecord: '否',
          dndState:'',
          extNbrPrefix: '',
        },
        extensionsEditForm: {
             uuid: '',
             vlanId: 0,
             extName: '',
             extNbr: '',
             password: '',
             domain: '',
             didNumber: '',
             groupId: -1,
             callFailedFlowId: '',
             enable: '',
             enableRecord: '否',
             dndState:'',
             extNbrPrefix: '',
        },
        extensionsBatchForm:{
             vlanId:'',
             groupId: '',
             startNumber: '',
             extensionsNumber: 100,
             extNbr: '',
             password: '',
             domain: '',
             dndState:'',
        },
        formLabelWidth: '80px',
        formRules: {      
          extNbr: [{required: true,message: '请输入分机号', trigger: 'blur'}],    
          password: [{required: true,message: '请输入密码', trigger: 'blur'}],
          vlanId: [{required: true,message: '请输入租户号(数字)',validator:checkNum, trigger: 'blur'}],
          callFailedFlowId: [{validator: checkNull, trigger: 'blur'}],
          enable: [{validator: checkNull, trigger: 'blur' }],
          groupId: [{ validator: check, trigger: 'blur'}],
          domain: [{required: true,message: '请输入域',trigger: 'blur'}]
        },
        batchformRules: {      
          vlanId: [{required: true,message: '请输入租户号(数字)',validator:checkNum, trigger: 'blur'}],
          extensionsNumber: [{required: true,validator: checkNumber, trigger: 'blur'}],
          extNbr: [{required: true,message: '请输入分机号', trigger: 'blur'}],    
          password: [{required: true,message: '请输入密码', trigger: 'blur'}],
          domain: [{required: true,message: '请输入域',trigger: 'blur'}]
        },
        options: [{
          value: ' ',
          label: '缺省'
        }],
        description: '',
        allGroup:{},
        isRecord:[{
          value: '0',
          label: '否'
          },{
          value: '1',
          label: '是'
        }],
        failedFlowId:[{
          value: -1,
          label: ' ',
        }],
        dndStateInfo:[{
          label: '是',
          value: 1,
        },{
          label: '否',
          value: 0,
        }],
        extraNumbers: [{
          value:-1,
          label:' ',
        }],
        currentUserVlanId:this.$store.baseStore.getters.getUserInfo.vlanId,
      }
    },
    props: {
        rebackButton: false,
        companyId: '',
    },
    watch:{
        companyId: function (val, oldVal){
            this.getextensionsList(); 
        },
       // getAllCompanyInfo(val) { //监听computed中的方法，当方法的返回值发生改变后会激活这个监听函数
         //   this.$data.allCompanyId= val;
        //}
    },
    computed: {
        allCompanyId:function() {
             return this.$store.baseStore.getters.getAllCompanyInfo;//allCompanyId依赖于getAllCompanyInfo，getAllCompanyInfo变了后allCompanyId会重新计算
        }
    },
    created() {
      this.getextensionsList(); //加载数据
      this.selectExtGroupByVlanId(this.$data.currentUserVlanId, null);
      this.selectAllFailedFlowId(this.$data.currentUserVlanId, null);//普通租户后端自动获取该租户下的呼叫失败信息
    },
    mounted(){
        var coms = this.$store.baseStore.getters.getAllCompanyInfo;
        if(coms == undefined || coms.length == 0){
            this.$store.baseStore.dispatch('getAllDeptId');
        }else{
            this.$data.allCompanyId= coms;
        }
        
    },
    components: {
      ElTableTreeColumn
    },
    methods: {
      //获取分页 总条数
      async getextensionsList() {
        try {
          let query = {
            admincompanyId:this.companyId,
            extNbr: this.searchInput,
            groupId: this.groupIdInput,
          }
          //分页
          const res = await extensionsList({
            pageindex:this.currentPage,
            pagesize:this.limit,
            query:JSON.stringify(query)
          });
          var extNbrs = '';
          if (res.resultCode == '0') {
            this.extensionsData = [];
            //防止后台数据幅值，，前台页面只需要改动此处就好，还有一个好处 就是只选择我需要的数据
            this.showCheck = res.result.data;
            this.count=res.result.total;
            res.result.data.forEach(item => {
              var isRecord = '否';
              if(item.enableRecord==1){
                  isRecord='是';
              }
              var dndState = '否';
              if(item.dndState == 1){
                dndState = '是';
              }
              if(item.callFailedFlowId == -1){
                item.callFailedFlowId = '';
              }
              if(item.groupId == -1){
                item.groupId = '';
              }
              const tableItem = {
                callFailedFlowId:item.callFailedFlowId,
                createAt:item.createAt,
                didNumber:item.didNumber,
                domain:item.domain,
                enable:item.enable,
                enableRecord:isRecord,
                extName:item.extName,
                extNbr:item.extNbr,
                groupId:item.groupId,
                password:item.password,
                updateAt:item.updateAt,
                uuid:item.uuid,
                vlanId:item.vlanId,
                dndState:dndState,
                extState:'未知',
              }
              extNbrs = extNbrs+item.extNbr+',';
              this.extensionsData.push(tableItem)
            })
            const states = await extensionsState({
              extNbrs : extNbrs,
            });
            if(states.resultCode == '0'){
              if(states.result.ext_state_count > 0){
                for(let i=0; i<this.extensionsData.length; i++){
                  for(let k in states.result.ext_state[0]){
                    if(this.extensionsData[i].extNbr == k){
                      let state = states.result.ext_state[0][k];
                      if(state == 'unregistered'){
                        state = '未注册';
                      } else if(state == 'registered'){
                        state = '已注册';
                      }
                      this.extensionsData[i].extState = state;
                    }
                  }
                }
              }
            } else {
              throw new Error(states.message)
            }
          } else {
            throw new Error(res.message)
          }
        } catch (err) {
          console.log('获取数据失败', err);
        }
      },
      //判断能否再添加录音分机
      async isFullRecordExt(vlanId) {
        try{
          const res = await canSelectRecord({vlanId:vlanId});
          if(res.resultCode =='0'){
            if(res.result){
              this.$data.cantSelectRecord = false;
            } else {
              this.$data.cantSelectRecord = true;
            }
          } else {
            throw new Error(res.message)
          }
        } catch (err) {
          console.log('获取数据失败', err);
        }
      },
      async selectExtGroupByVlanId(vlanId, formName) {
        try {
          if((this.companyId != undefined && this.companyId != '') && formName == null){//判断是否从租户设置进入并且不是添加页面和编辑页面
              vlanId = this.companyId;
            }
            const res = await selectExtGroupByVlanId({vlanId:vlanId});
            var options = [{
              value:' ',
              label:'缺省',
            }];
            this.$data.allGroup = {};
            if (res.resultCode == '0') {
                for (var value of res.result){
                   var temp = {};
                   temp.label = value.description;
                   temp.value = value.groupId;
                   options.push(temp);
                   this.$data.allGroup[temp.value]=temp.label;
                }
                this.$data.options = options;
                this.$data.allGroup[-1]='缺省';
            }else {
               throw new Error(res.result)
            }
        } catch (err) {
            console.log('获取数据失败', err);
        }
      },

      async selectAllFailedFlowId(vlanId, formName) {
        try {
            if((this.companyId != undefined && this.companyId != '') && formName == null){//判断是否从租户设置进入并且不是添加页面和编辑页面
              vlanId = this.companyId;
            }
            const res = await selectAllFailedFlowId({
              vlanId:vlanId,
            });
            var failedFlowId = [{
              value: -1,
              label: ' ',
            }];
            if (res.resultCode == '0') {
                for (var value of res.result){
                  if(value.callFailedFlowId == -1){
                    continue;
                  }
                  var temp = {};
                  temp.label = value.callFailedFlowId;
                  temp.value = value.callFailedFlowId;
                  failedFlowId.push(temp);
                }
                this.$data.failedFlowId = failedFlowId;
            }else {
               throw new Error(res.result)
            }
        } catch (err) {
            console.log('获取数据失败', err);
        }
      },

      async getNotOccuptNumbers(vlanId, formName) {
        try {
            if((this.companyId != undefined && this.companyId != '') && formName == null){
              vlanId = this.companyId
            }
            const res = await getNotOccuptNumber({
              vlanId:vlanId,
            });
            if (res.resultCode == '0') {
                this.extraNumbers=[{
                  value:'',
                  label:'',
                }];
                if(res.result != undefined){
                    var arr =  res.result;
                    for(var i=0;i<arr.length;i++){
                        var obj = new Object();
                        obj['label'] = arr[i];
                        obj['value'] =  arr[i];
                        this.extraNumbers.push(obj);
                    }
                }
            }else {
               throw new Error(res.result)
            }
        } catch (err) {
            console.log('获取数据失败', err);
        }
      },

      groupChange(value){
        if(value == ' '){
          this.$data.description = '缺省';
        } else {
          this.$data.description = this.$data.allGroup[value];
        }
      },
     
      handSaveParam(args){
         if(args.enable == undefined || args.enable === '')delete args.enable;
         if(args.callFailedFlowId == undefined || args.callFailedFlowId === '')delete args.callFailedFlowId;
         if(args.groupId == undefined || args.groupId === '')delete args.groupId;
         if(args.groupId == ' ') args.groupId = -1;
         args.extNbr = args.extNbrPrefix + args.extNbr;
         if(args.extName == ''){
           args.extName = args.extNbr;
         }
         if(args.enableRecord == '是'){
            args.enableRecord = 1;
         }else{
            args.enableRecord = 0;
         }
         return args;
      },
      handleSizeChange(val) {
      },
      handleCurrentChange(val) {
        this.currentPage = val;
        this.getextensionsList();
      },
      handleSelectionChange(val) {
        this.multipleSelection = val;
      },
      handleExtensionsSearch() {
        this.getextensionsList();
      },
      dataFormat(row, column) {
         var time =  row[column.property];
         if(time != undefined && time != ''){
            return moment(time).format("YYYY-MM-DD HH:mm:ss")  
         }else{
            return '';
         }
         
      },
      //获取租户固定前缀
      async getExtNbrPrefix(vlanId, formName){
        try{
          var res = await getExtNbrPrefix({
            vlanId:vlanId,
          });
          if(res.resultCode == '0'){
            if(formName == 'extensionsForm'){
              this.$data.extensionsForm.extNbrPrefix = res.result;
            } else if(formName == 'extensionsEditForm'){
              this.$data.extensionsEditForm.extNbrPrefix = res.result;
            } else {
              this.$data.extensionsBatchForm.extNbr = res.result;
            }
          } else {
            throw new Error(res.message)
          }
        } catch (err) {
          console.log(err)
        }
      },
      //添加编辑和批量添加的租户改变时运行
      async vlanIdChange(formName){
        if(this.$store.baseStore.getters.getUserInfo.managerLevel == '999'){
          if(formName == 'extensionsForm'){
            this.selectAllFailedFlowId(this.$data.extensionsForm.vlanId,formName);//获取输入的租户号的呼叫失败原因列表
            this.getNotOccuptNumbers(this.$data.extensionsForm.vlanId,formName);//获取租户的可用外显号码
            this.selectExtGroupByVlanId(this.$data.extensionsForm.vlanId,formName);
            this.getExtNbrPrefix(this.$data.extensionsForm.vlanId, formName);
          } else if(formName == 'extensionsEditForm'){
            this.selectAllFailedFlowId(this.$data.extensionsEditForm.vlanId,formName);//获取输入的租户号的呼叫失败原因列表
            this.getNotOccuptNumbers(this.$data.extensionsEditForm.vlanId,formName);//获取租户的可用外显号码
            this.selectExtGroupByVlanId(this.$data.extensionsEditForm.vlanId,formName);
            this.getExtNbrPrefix(this.$data.extensionsEditForm.vlanId, formName);
          } else {
            this.getExtNbrPrefix(this.$data.extensionsBatchForm.vlanId, formName);
            this.selectExtGroupByVlanId(this.$data.extensionsBatchForm.vlanId,formName);
          }
        }
      },
      //增加
      async handleExtensionsAdd() {
        var tempVlanId = this.$data.currentUserVlanId;
        if(this.companyId != undefined && this.companyId != ''){
            tempVlanId = parseInt(this.companyId);
        }
        this.extensionsForm = {
          vlanId: tempVlanId,
          extName: '',
          extNbr: '',
          password: '',
          domain: '',
          didNumber: '',
          groupId: ' ',
          callFailedFlowId: -1,
          enable: '',
          enableRecord: '否',
          dndState: 0,
          extNbrPrefix:'',
        };
        this.$data.description= '缺省';
        if(this.$store.baseStore.getters.getUserInfo.managerLevel != '999' || (this.companyId == undefined || this.companyId == '')){
          this.isFullRecordExt(this.$data.currentUserVlanId);
          this.getExtNbrPrefix(this.$data.currentUserVlanId,'extensionsForm');
          this.getNotOccuptNumbers(this.$data.currentUserVlanId,null);
        } else {
          this.isFullRecordExt(this.companyId);
          this.getNotOccuptNumbers(this.companyId,null);
          this.selectAllFailedFlowId(this.companyId,'extensionsForm');
          this.getExtNbrPrefix(this.companyId,'extensionsForm');
          this.selectExtGroupByVlanId(this.companyId,'extensionsForm');
        }
        this.dialogFormVisible = true;
      },
      //批量增加
      async handleBatchAdd() {
        var tempVlanId = this.$data.currentUserVlanId;
        if(this.companyId != undefined && this.companyId != ''){
            tempVlanId = parseInt(this.companyId);
        }
        this.extensionsBatchForm = {
             vlanId:tempVlanId,
             groupId:' ',
             startNumber:'001',
             extensionsNumber: 100,
             extNbr: '',
             password: '',
             domain: '',
             dndState: 0,
        };
        this.$data.description= '缺省';
        if(this.$store.baseStore.getters.getUserInfo.managerLevel != '999' || (this.companyId == undefined || this.companyId == '')){
          this.getExtNbrPrefix(this.$data.currentUserVlanId,'extensionsBatchForm');
        } else {
          this.getExtNbrPrefix(this.companyId,'extensionsBatchForm');
        }
        this.dialogBatchFormVisible = true;
      },
      //关闭弹框时重置表单
      handleDialogWithFormClose() {
        for (var prop in this.$refs) {
          if (this.$refs.hasOwnProperty(prop)) {
            if (this.$refs[prop].$vnode.tag == 'vue-component-48-ElForm') {
              this.$refs[prop].resetFields();
            }
          }
        }
        this.$data.description='';
      },
      saveResetPassword(){
        let that = this;
        this.$confirm('此操作将重置所有已选分机密码, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(async() => {
          try {
            let resetIdArr = [];
            that.multipleSelection.forEach(s => {
              resetIdArr.push(s.extNbr)
            })
            let resetIdsString = resetIdArr.join(',');
            let data = {
              extNbr: resetIdsString,
              password: this.restFrom.resetPassword,
            }
            //此方法可以和后台协商一致，单条与批量删除使用同一个函数
            const result = await resetExtPasswordByExtNbr(data);
            if (result.resultCode == '0') {
              that.getextensionsList();
              that.dialogResetPassword = false;
              this.$message({
                type: 'success',
                message: '重置成功!'
              });
            }
          } catch (err) {
            console.log(err)
          }
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消重置'
          });
        });
      },
      handleBatchResetPassword(){
        if (this.multipleSelection.length === 0) {
          this.$alert('请至少选择一条记录', '提示', {
            confirmButtonText: '确定',
            callback: () => {
              return false;
            }
          });
        } else {
          this.dialogResetPassword = true;
          this.restFrom.resetPassword = '';
        }
      },
      //批量删除
      handleBatchDelete() {
        let that = this;
        if (this.multipleSelection.length === 0) {
          this.$alert('请至少选择一条记录', '提示', {
            confirmButtonText: '确定',
            callback: () => {
              return false;
            }
          });
        } else {
          this.$confirm('此操作将永久删除记录, 是否继续?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(async() => {
            try {
              let deletedIdArr = [];
              that.multipleSelection.forEach(s => {
                deletedIdArr.push(s.uuid)
              })
              let deletedIdsString = deletedIdArr.join(',');
              let data = {
                uuid: deletedIdsString
              }
              //此方法可以和后台协商一致，单条与批量删除使用同一个函数
              const result = await deleteextensionsByIds(data);
              if (result.resultCode == '0') {
                that.getextensionsList();
                this.$message({
                  type: 'success',
                  message: '删除成功!'
                });
              }
            } catch (err) {
              console.log(err)
            }
          }).catch(() => {
            this.$message({
              type: 'info',
              message: '已取消删除'
            });
          });
        }
      },
      //删除当前记录
      handleExtensionsDelete(index, row) {
        let that = this;
        this.$confirm('此操作将永久删除记录, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(async() => {
          try {
            let deletedIdsString = [row.uuid].join(',');
            let data = {
              uuid: deletedIdsString
            }
            //此方法可以和后台协商一致，单条与批量删除使用同一个函数
            const result = await deleteextensionsByIds(data);
            if (result.resultCode == '0') {
              that.getextensionsList();
              this.$message({
                type: 'success',
                message: '删除成功!'
              });
            }
          } catch (err) {
            console.log(err)
          }
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });
      },
      //编辑
      async handleExtensionsEdit(index, row) {
        this.dialogFormEdit = true;
        this.getNotOccuptNumbers(row.vlanId,'extensionsEditForm');
        this.selectExtGroupByVlanId(row.vlanId,'extensionsEditForm');
        this.selectAllFailedFlowId(row.vlanId,'extensionsEditForm');
        var dndState = 0;
        if(row.dndState == '是'){
          dndState = 1;
        }
        if (typeof row !== 'undefined') {
            this.extensionsEditForm.vlanId = row.vlanId;
            this.extensionsEditForm.extNbr = row.extNbr;
            if(row.extName != row.extNbr){
              this.extensionsEditForm.extName = row.extName;
            } else {
              this.extensionsEditForm.extName = '';
            }
            this.extensionsEditForm.password = row.password;
            this.extensionsEditForm.domain = row.domain;
            this.extensionsEditForm.didNumber = row.didNumber;
            this.extensionsEditForm.groupId = row.groupId;
            if(row.groupId == ""){
              this.extensionsEditForm.groupId = ' ';
              this.$data.description= '缺省';
            } else {
              this.extensionsEditForm.groupId = row.groupId;
            }
            if(row.callFailedFlowId == ""){
              this.extensionsEditForm.callFailedFlowId = -1;
            } else {
              this.extensionsEditForm.callFailedFlowId = row.callFailedFlowId;
            }
            this.extensionsEditForm.enable = row.enable;
            this.extensionsEditForm.enableRecord = row.enableRecord;
            this.extensionsEditForm.uuid = row.uuid;
            this.extensionsEditForm.dndState = dndState;
            try{
              var res = await getExtNbrPrefix({
                vlanId:row.vlanId,
              });
              if(res.resultCode == '0'){
                this.extensionsEditForm.extNbrPrefix = res.result;
                this.extensionsEditForm.extNbr = row.extNbr.replace(res.result,"");
              } else {
                throw new Error(res.message)
              }
            } catch (err) {
              console.log(err)
            }
        }
        this.$data.cantSelectRecord = false;
      },
      //查看
      handleextensionsCheck(index, row) {
        this.dialogFormCheck = true;
        row.createAt=this.dateFormat(row.createAt);
        row.updateAt=this.dateFormat(row.updateAt);
        this.$data.showCheck = row;
      },
      dateFormat(time) {
         if(time != undefined && time != ''){
            return this.$moment(time).format("YYYY-MM-DD HH:mm:ss");
         }else{
            return '';
         }
         
      },
      //添加 编辑的 保存
      handleExtensionsSave(formName) {
        let that = this;
        this.$refs[formName].validate(async(valid) => {
          var args = this.$refs[formName].model;
          if (valid) {
            try {
              //数据拿出来，如果为空则去掉
              var vlanID = this.$data.currentUserVlanId;
              if(this.$store.baseStore.getters.getUserInfo.managerLevel=='999'){//超级管理员需要输入租户ID
                vlanID = args.vlanId;
              }
              var extCount = await getExtCountByVlanId({//获取租户下剩余和注册分机数量
                vlanId:vlanID,
              });
              if(formName == 'extensionsEditForm'){
                var canRecord = await canSelectRecord({//编辑的保存时，当该分机原本是录音分机时，需要排除该分机
                  vlanId:vlanID,
                  uuid:args.uuid,
                });
              } else {
                var canRecord = await canSelectRecord({vlanId:vlanID});//得到当前租户剩余可添加留言分机数量
              }
              if(args.enableRecord == '是' && canRecord.result <= 0 ){
                this.reduction(formName, args);
                this.$message({
                     type: 'error',
                     message: '当前租户下录音分机数量已达上限'
                });
              } else if(extCount.result > 0  || (formName == 'extensionsEditForm' && extCount.result >= 0)){
                var params = this.handSaveParam(args);
                const result = await saveextensions(params);
                if (result.resultCode == '0') {
                    if(formName == 'extensionsForm'){//添加
                          that.dialogFormVisible = false;
                    }else{
                      this.dialogFormEdit = false;
                    }
                    that.getextensionsList();
                    this.$message({
                        type: 'success',
                        message: '分机保存成功'
                    });
                }else if(result.resultCode == '00009'){
                  this.reduction(formName, args);
                    this.$message({
                      type: 'error',
                      message: '分机号不能重复'
                    });
                }else{
                  this.reduction(formName, args);
                    this.$message({
                      type: 'error',
                      message: '系统报错请联系管理员'
                    });
                }
              } else {
                this.reduction(formName, args);
                this.$message({
                  type: 'error',
                  message: '当前租户下分机数量已达上限'
                });
              }
              
            } catch (e) {
              this.reduction(formName, args);
             console.log(e);
               this.$message({
                  type: 'error',
                  message: '保存失败'
               });
            }
          } else {
            this.reduction(formName, args);
             this.$message({
                type: 'info',
                message: '请检查输入是否不合法'
             });
            return false;
          }
        });
      },
      //保存失败时，将已经添加的前缀删除，以及其他项的还原
      reduction(formName,args){
        if(formName == 'extensionsEditForm'){ 
          if(this.$data.extensionsEditForm.extName == this.$data.extensionsEditForm.extNbr){
            this.$data.extensionsEditForm.extName = '';
          }
          this.$data.extensionsEditForm.extNbr = this.$data.extensionsEditForm.extNbr.replace(args.extNbrPrefix,"");
        }
        if(formName == 'extensionsForm'){ 
          if(this.$data.extensionsForm.extName == this.$data.extensionsForm.extNbr){
            this.$data.extensionsForm.extName = '';
          }
          this.$data.extensionsForm.extNbr = this.$data.extensionsForm.extNbr.replace(args.extNbrPrefix,"");
        }
        if(args.groupId == -1){
          args.groupId = ' ';
        }
        if(args.enableRecord == 1){
            args.enableRecord = '是';
        }else if(args.enableRecord == 0){
            args.enableRecord = '否';
        }
      },

      //批量添加
      handleBatchSave(formName) {
        let that = this;
        this.$refs[formName].validate(async(valid) => {
          if (valid) {
            try {
              var args = this.$refs[formName].model;
              if(args.groupId == ' '){
                args.groupId = -1;
              }
              var vlanID = this.$data.currentUserVlanId;
              if(this.$store.baseStore.getters.getUserInfo.managerLevel=='999'){
                vlanID = args.vlanId;
              }
              var extCount = await getExtCountByVlanId({
                vlanId:vlanID,
              });
              if (extCount.result < args.extensionsNumber){
                if(args.groupId == -1){
                  args.groupId = ' ';
                }
                this.$message({
                     type: 'error',
                     message: '添加分机数过多,超过上限'
                });
              } else {
                const result = await batchsaveextensions(args);
                if (result.resultCode == '0') {
                    this.dialogBatchFormVisible = false;
                    that.getextensionsList();
                    this.$message({
                      type: 'success',
                      message: '分机保存成功'
                    });
                }else if(result.resultCode == '00009'){
                    if(args.groupId == -1){
                      args.groupId = ' ';
                    }
                    this.$message({
                      type: 'error',
                      message: '分机号不能重复'
                    });
                }else if(result.resultCode == '91005'){
                    if(args.groupId == -1){
                      args.groupId = ' ';
                    }
                    this.$message({
                      type: 'error',
                      message: '开始序号过短，请重新输入'
                    });
                }else{
                    if(args.groupId == -1){
                      args.groupId = ' ';
                    }
                    this.$message({
                      type: 'error',
                      message: '系统报错请联系管理员'
                    });
                }
              }
              
            } catch (e) {
                console.log(e);
                if(args.groupId == -1){
                  args.groupId = ' ';
                }
               this.$message({
                  type: 'info',
                  message: '保存失败'
               });
            }
          } else {
             this.$message({
                type: 'info',
                message: '请检查输入是否不合法'
             });
            return false;
          }
        });
      },
      rebackEvent(){
          var args =  new Object();
          this.$emit('rebackEvent',args);
      },
    }
  }
</script>
